.sign-in {
  display: flex;
  flex-direction: column;

  margin-right: 2em;
  max-width: 40vw;
  flex: 1;

  .title {
    margin: 0.6em 0;
  }
  form {
    width: auto;

    .submit-container {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      justify-content: space-between;
      gap: 0.3em;

      :first-child {
        flex-grow: 2;
      }

      :last-child {
        flex-grow: 3;
      }
    }
  }
}
